.dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    font-size: 16px;
    border: none;
    cursor: pointer;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
    top: -50px;
  }  
  .dropdown-content {
    display: none;
    position: absolute;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    border: none;
    outline: none;
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    background-color: #3e8e41;
  }
  .drop {
    display: block;
    width: 100%;
    padding: 7px;
    outline: none;
    background-color: rgb(231, 56, 56);
    border-bottom: green;
    color: #fff;
  }
  .drop:hover {
    background-color: #3e8e41;
  }
