* {
  box-sizing: border-box;
}
body {
  width: 100%;
  background-color: rgb(100, 100, 196);
  color: white;
}
.small {
  font-size: medium;
}
.tag {
  text-align: center;
}
.container {
  margin: 0 auto;
  width: 80%;
}
ul {
  list-style-type: none;
  cursor: pointer;
}
li:hover {
  color: #3e8e41;
  background: #ede;
  cursor: pointer;
}
input {
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  outline: none;
  border: none;

}
.scroll-to-top {
  position:fixed;
  bottom: 12px;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  right: 5%;
  background: #3e8e41;
  color: #fff;
}
.scroll-to-top .icon {
  margin-top:10px;
  animation: scrollTop 0.5s alternate ease infinite;
}
@keyframes scrollTop {
  from{
      transform: translateY(2px);
  }
  to {
      transform: translateY(-1px);
  }
}
.scroll-to-bottom {
  position: relative;
  bottom: 12px;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  top: -59px;
  left: 100%;
  background: #3e8e41;
  color: #fff;
}
.scroll-to-bottom .icon {
  margin-top:10px;
  margin-left: 15px;
  animation: scrollTop 0.5s alternate ease infinite;
}
@keyframes scrollTop {
  from{
      transform: translateY(2px);
  }
  to {
      transform: translateY(-1px);
  }
}
.copyright {
  text-align: center;
}